/* ==============
Panels
===================*/
.panel {
-moz-box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.1);
-webkit-box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.1);
border-radius: 0px;
border: none;
box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.1);
margin-bottom: 20px;
}
.panel .panel-body {
padding: 20px;
}
.panel .panel-body p {
margin: 0px;
}
.panel .panel-body p + p {
margin-top: 15px;
}
.panel-heading {
border-radius: 0;
border: none !important;
padding: 10px 20px;
}
.panel-default > .panel-heading {
background-color: #fafafa;
border-bottom: none;
color: #797979;
}
.panel-title {
font-size: 14px;
font-weight: 600;
margin-bottom: 0;
margin-top: 0;
text-transform: uppercase;
}
.panel-footer {
background: #fafafa;
border-top: 0px;
}
.panel-color .panel-title {
color: #ffffff;
}
.panel-primary > .panel-heading {
background-color: #6e8cd7;
}
.panel-success > .panel-heading {
background-color: #33b86c;
}
.panel-info > .panel-heading {
background-color: #29b6f6;
}
.panel-warning > .panel-heading {
background-color: #ffd740;
}
.panel-danger > .panel-heading {
background-color: #ef5350;
}
.panel-purple > .panel-heading {
background-color: #7e57c2;
}
.panel-pink > .panel-heading {
background-color: #ec407a;
}
.panel-inverse > .panel-heading {
background-color: #212121;
}
.panel-border {
border-radius: 3px;
}
.panel-border .panel-heading {
background-color: #ffffff;
border-top: 3px solid #ccc !important;
border-radius: 3px;
padding: 10px 20px 0px;
}
.panel-border .panel-body {
padding: 15px 20px 20px 20px;
}
.panel-border.panel-primary .panel-heading {
border-color: #6e8cd7 !important;
color: #6e8cd7 !important;
}
.panel-border.panel-success .panel-heading {
border-color: #33b86c !important;
color: #33b86c !important;
}
.panel-border.panel-info .panel-heading {
border-color: #29b6f6 !important;
color: #29b6f6 !important;
}
.panel-border.panel-warning .panel-heading {
border-color: #ffd740 !important;
color: #ffd740 !important;
}
.panel-border.panel-danger .panel-heading {
border-color: #ef5350 !important;
color: #ef5350 !important;
}
.panel-border.panel-purple .panel-heading {
border-color: #7e57c2 !important;
color: #7e57c2 !important;
}
.panel-border.panel-pink .panel-heading {
border-color: #ec407a !important;
color: #ec407a !important;
}
.panel-border.panel-inverse .panel-heading {
border-color: #212121 !important;
color: #212121 !important;
}
.panel-fill {
border-radius: 3px;
}
.panel-fill .panel-heading {
background-color: transparent;
color: #ffffff;
border-bottom: 1px solid rgba(255, 255, 255, 0.5) !important;
}
.panel-fill .panel-body {
color: rgba(255, 255, 255, 0.85);
}
.panel-fill.panel-default .panel-body {
color: #666;
}
.panel-fill.panel-default .panel-heading {
background-color: transparent;
color: #333333;
border-bottom: 1px solid rgba(0, 0, 0, 0.1) !important;
}
.panel-fill.panel-primary {
background-color: #489ce7;
}
.panel-fill.panel-success {
background-color: #58c386;
}
.panel-fill.panel-info {
background-color: #50c1f4;
}
.panel-fill.panel-warning {
background-color: #fcdc63;
}
.panel-fill.panel-danger {
background-color: #ef7270;
}
.panel-fill.panel-purple {
background-color: #9475cb;
}
.panel-fill.panel-pink {
background-color: #ec6391;
}
.panel-fill.panel-inverse {
background-color: #4a4a4a;
}
.panel-group .panel .panel-heading a[data-toggle=collapse].collapsed:before {
content: '\f067';
}
.panel-group .panel .panel-heading .accordion-toggle.collapsed:before {
content: '\f067';
}
.panel-group .panel .panel-heading a[data-toggle=collapse] {
display: block;
}
.panel-group .panel .panel-heading a[data-toggle=collapse]:before {
content: '\f068';
display: block;
float: right;
font-family: 'FontAwesome';
font-size: 14px;
text-align: right;
width: 25px;
}
.panel-group .panel .panel-heading .accordion-toggle {
display: block;
}
.panel-group .panel .panel-heading .accordion-toggle:before {
content: '\f068';
display: block;
float: right;
font-family: 'FontAwesome';
font-size: 14px;
text-align: right;
width: 25px;
}
.panel-group .panel .panel-heading + .panel-collapse .panel-body {
border-top: none;
}
.panel-group .panel-heading {
padding: 12px 26px;
}
.panel-group.panel-group-joined .panel + .panel {
border-top: 1px solid #eeeeee;
margin-top: 0;
}
.panel-group-joined .panel-group .panel + .panel {
border-top: 1px solid #eeeeee;
margin-top: 0;
}
/* ==============
Profile
===================*/
.bg-overlay {
-moz-border-radius: 6px 6px 0px 0px;
-webkit-border-radius: 6px 6px 0px 0px;
background-color: rgba(49, 126, 235, 0.4);
border-radius: 6px 6px 0px 0px;
height: 100%;
left: 0px;
position: absolute;
top: 0px;
width: 100%;
}
.bg-picture {
-webkit-background-size: cover;
background-position: center;
background-repeat: no-repeat;
background-size: cover;
background:#00b5ec;
margin-top: -22px;
padding: 100px 0px;
position: relative;
}
.bg-picture > .bg-picture-overlay {
background-image: -o-linear-gradient(top, rgba(255, 255, 255, 0) 0, rgba(0, 0, 0, 0.4) 100%);
background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0)), to(rgba(0, 0, 0, 0.4)));
background-image: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0, rgba(0, 0, 0, 0.4) 100%);
background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0, rgba(0, 0, 0, 0.4) 100%);
bottom: 0;
left: 0;
position: absolute;
right: 0;
top: 0;
}
.profile-info-name {
position: relative;
}
.profile-tab-content {
background-color: transparent !important;
box-shadow: none !important;
margin-top: 35px;
}
.user-tabs {
background-color: #ffffff;
box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
margin-left: 0;
margin-right: 0px;
}
.user-tabs .nav.nav-tabs {
box-shadow: none !important;
}
.user-tabs .nav.nav-tabs a {
text-transform: uppercase;
}
.user-tabs .pull-right .btn {
margin-top: 8px;
}
.about-info-p {
margin-bottom: 20px;
}
.about-info-p p {
font-size: 16px;
}
.tabs {
background-color: #ffffff;
margin: 0 auto;
padding: 0px;
position: relative;
white-space: nowrap;
width: 100%;
}
.tabs li.tab {
background-color: #ffffff;
display: block;
float: left;
margin: 0;
text-align: center;
}
.tabs li.tab a {
-moz-transition: color 0.28s ease;
-ms-transition: color 0.28s ease;
-o-transition: color 0.28s ease;
-webkit-transition: color 0.28s ease;
color: #ee6e73;
display: block;
height: 100%;
text-decoration: none;
transition: color 0.28s ease;
width: 100%;
}
.tabs li.tab a.active {
color: #6e8cd7 !important;
}
.tabs .indicator {
background-color: #6e8cd7;
bottom: 0;
height: 2px;
position: absolute;
will-change: left, right;
}
.tabs-top .indicator {
top: 0;
}
.nav.nav-tabs + .tab-content {
background: #ffffff;
margin-bottom: 30px;
padding: 30px;
}
.tabs-vertical-env {
background-color: #eeeeee;
margin-bottom: 30px;
}
.tabs-vertical-env .tab-content {
background: #ffffff;
display: table-cell;
margin-bottom: 30px;
padding: 30px;
vertical-align: top;
}
.tabs-vertical-env .nav.tabs-vertical {
display: table-cell;
min-width: 120px;
vertical-align: top;
width: 150px;
}
.tabs-vertical-env .nav.tabs-vertical li.active > a {
background-color: #ffffff;
border: 0;
}
.tabs-vertical-env .nav.tabs-vertical li > a {
color: #333333;
text-align: center;
white-space: nowrap;
}
.nav.nav-tabs > li.active > a {
background-color: #ffffff;
border: 0;
}
.nav.nav-tabs > li > a,
.nav.tabs-vertical > li > a {
background-color: transparent;
border-radius: 0;
border: none;
color: #333333 !important;
cursor: pointer;
line-height: 50px;
font-weight: 700;
padding: 0px 20px;
}
.nav.nav-tabs > li > a:hover,
.nav.tabs-vertical > li > a:hover {
color: #6e8cd7 !important;
}
.tab-content {
box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
color: #777777;
}
.nav.nav-tabs > li:last-of-type a {
margin-right: 0px;
}
.nav.nav-tabs {
border-bottom: 0;
box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
}
.navtab-bg {
background-color: #eeeeee;
}
.nav-tabs.nav-justified > .active > a,
.nav-tabs.nav-justified > .active > a:hover,
.nav-tabs.nav-justified > .active > a:focus,
.tabs-vertical-env .nav.tabs-vertical li.active > a {
border: none;
}
.nav-tabs > li.active > a,
.nav-tabs > li.active > a:focus,
.nav-tabs > li.active > a:hover,
.tabs-vertical > li.active > a,
.tabs-vertical > li.active > a:focus,
.tabs-vertical > li.active > a:hover {
color: #6e8cd7 !important;
}



